@use "global" as *;

.card__body {
  padding-top: rem(4);
  padding-left: rem(23);
  padding-right: rem(23);
  letter-spacing: rem(1);
  font-weight: $normal;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card__img {
  aspect-ratio: 335 / 223;
  object-fit: cover;
  overflow: hidden;
  position: relative;
}

.card__img img {
  object-fit: cover;
  height: 100%;
}

.card__title {
  font-size: rem(18);
  font-weight: 500;
  text-align: left;
  width: 100%;
  line-height: calc(34 / 18);
  letter-spacing: calc(1 / 18) * 1em;
  @include mq(md) {
    line-height: 2;
  }
}

.card__text {
  font-size: rem(14);
  margin-top: rem(5);
  line-height: calc(26 / 14);
  text-align: left;
  letter-spacing: calc(1 / 14) * 1em;
  flex-grow: 1;
  @include mq(md) {
    margin-top: rem(3);
    line-height: 2;
  }
}

.card__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: rem(14);
  line-height: calc(26 / 14);
  margin-top: auto;
  padding: rem(7) rem(23) rem(16);
}
.card__category {
  background-color: #ffea2e;
  color: $black;
  padding: 0 rem(5);
  letter-spacing: calc(1 / 14) * 1em;
}
